* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

body::-webkit-scrollbar {
  display: none;
}

.hidden {
  position: absolute;
  left: -9999px;
}

:root {
  --baseColor: white;
  /* 주조색 - 약 70% 사용 */
  --dominantColor: black;
  /* 보조색 - 약 20% 사용 */
  --accentColor: skyblue;
  /* 강조색 - 약 10% 사용 */
}





/*************** 헤더 영역 ***************/

header {
  text-align: center;
  background-color: var(--dominantColor);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 4em;
  z-index: 10;
}

h1 {
  font: bold 2em/2 '맑은 고딕';
  /* font 속성 사용시 서체 반드시 입력 - 서체를 '' 형식으로 비워놓으면 build할 때 자동 삭제 */
}

/* Link 태그는 a태그와 동일 */

h1 a:any-link {
  text-decoration: none;
  color: var(--accentColor);
}

.gnb {
  position: absolute;
  right: 1em;
  top: 1em;
}

.gnb li {
  float: left;
  list-style: none;
  margin-left: .5em;
}

.material-symbols-outlined {
  font-size: 2em;
  color: var(--baseColor);
}






/*************** 주 메뉴 ***************/
.Lnb {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  z-index: 10;
}

.bottomAppBar {
  height: 80px;
  background-color: var(--dominantColor);
  position: relative;
  border-top: 0.1em solid var(--accentColor);
  z-index: 10;
}

.bottomAppBar>li {
  list-style: none;
  float: left;
  width: 50%;
  height: 100%;
  text-align: center;
}

.bottomAppBar a {
  display: block;
  color: var(--accentColor);
  text-decoration: none;
  font: bold 1.4em/80px '맑은 고딕';
}

.bottomAppBar>li:nth-child(2) a {
  padding-right: 1em;
}

.bottomAppBar>li:nth-child(3) a {
  padding-left: 1em;
}

li.menuBtn {
  position: absolute;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  left: 50%;
  top: -50%;
  transform: translateX(-50%);
  background-color: var(--accentColor);
  overflow: hidden;
}

.menuBtn span {
  display: block;
  height: 100%;
  padding-top: 20px;
  font: bold 1.4em/1 '맑은 고딕';
  cursor: pointer;
  box-sizing: border-box;
}



/********* 목차 *********/
.menu {
  width: 100%;
  height: calc(100vh - 9em);
  background-color: var(--dominantColor);
  position: fixed;
  padding: 1em;
  top: 4em;
  left: 0;
  z-index: 9;
  border-top: 0.1em solid var(--accentColor);
  box-sizing: border-box;
  transition: all .4s;
}

.slideUp {
  top: 4em;
}

.slideDown {
  top: calc(100vh - 80px);
}

.menu a {
  display: block;
  padding-left: 1em;
  font: bold 1.2em/3 '맑은 고딕';
  color: var(--baseColor);
  text-decoration: none;
}

.subMenu {
  padding-bottom: 1em;
}

.subMenu a {
  padding-left: 2em;
  font: normal 1em/2 '맑은 고딕';
}








/*************** Section_공통 ***************/

section {
  margin-top: 5em;
  min-height: calc(100vh - 9em);
}

h2 {
  font-size: 1em;
  text-align: center;
  height: 3em;
  color: var(--accentColor);
}

.subTitle {
  font-size: 2em;
  height: 2em;
  margin-top: 1em;
}

article,
aside {
  padding: 1em;
}

h3 {
  margin-bottom: 1em;
}

h5 {
  font-size: 1em;
}

article ul,
article ol,
article>p {
  padding-left: 2em;
}

.overview {
  padding: .5em;
}

.tip {
  padding: .5em 1em;
}

.example {
  width: fit-content;
  color: var(--accentColor);
  padding: 0 1em;
  margin: 1em;
  border: .2em solid;
}

.example>li {
  list-style: none;
  margin: 1em 0;
}

.example span {
  margin-left: 1em;
  font-weight: bold;
}

.func {
  padding-left: 1em;
  list-style: none;
}

.func p {
  padding-left: 1em;
}



/*************** Start.jsx ***************/

/*** 리액트 앱 구조 ***/
table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}

caption {
  height: 2em;
  text-align: left;
}

th,
td {
  border: .1em solid var(--dominantColor);
  padding: .5em;
}

thead th {
  background-color: #666;
  color: var(--baseColor);
}

tbody th {
  text-align: left;
  background-color: #ddd;
}

.point {
  font-weight: bold;
  color: var(--accentColor);
}

.del {
  color: #ddd;
}

.center {
  text-align: center;
}



/*************** Routing.jsx ***************/
.nest {
  width: fit-content;
  grid-gap: .5em;
  margin: 0 auto;
  overflow: hidden;
}

.nest>li {
  float: left;
  margin: 0 .5em;
  list-style: none;
}

.nest a {
  display: block;
  text-decoration: none;
  color: var(--baseColor);
  background-color: var(--accentColor);
  text-align: center;
  padding: .5em;
}





/*************** Img.jsx ***************/
.Img img {
  width: 100%;
  vertical-align: top;
  max-width: 600px;
  margin: 1em 0;
}

.img {
  max-width: 600px;
  height: 200px;
  background-image: url(./img/photo-04.jpg);
  background-size: cover;
  background-position: center;
  margin: 1em 0;
}






/*************** Map.jsx ***************/
.contents {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-rows: 1fr 3fr;
  grid-gap: 1em;
  height: 64vw;
  padding: 1em 0;
  border: .1em solid #ddd;
  border-radius: 1em;
  margin-bottom: 1em;
}

.contents>div {
  /* background-color: #ddd; */
  overflow: hidden;
}

.userPhoto {
  border-radius: 0 .5em .5em 0;
}

.userPhoto img, .photo img {
  position: relative;
  width: 150%;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.title {
  padding-top: 0.3em;
}

.photo {
  border-radius: .5em 0 0 .5em;
}

.photo img {
  width: 100%;
}





/*************** Hooks - State.jsx ***************/

.Modal {
  position: fixed;
  right: 0;
  top: 0;
  width: 60%;
  height: 100vh;
  background-color: #000a;
  z-index: 10;
  transition: all .5s;
}

.Modal h4 {
  text-align: center;
  font: bold 4em/100vh '맑은 고딕';
  padding: 0;
}

.ModalShow {
  transform: scale(1);
}

.ModalHide {
  transform: scale(0);
}






/*************** 푸터 영역 ***************/
footer {
  background-color: var(--dominantColor);
  height: 12em;
}

footer h2 a {
  color: var(--baseColor);
  text-decoration: none;
  line-height: 3em;
}








/*************** 모바일에서 글자 크기 작게 ***************/
@media all and (max-width:412px) {
  body {
    font-size: 14px;
  }
}